  padding: 10px;
  margin: 10px;
  border-radius: 10px;
}

.caard p {
  font-family: 'Neutrif Studio', sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 29.26px;
  text-align: left;
  color: rgba(28, 31, 64, 0.8);
}

