body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  user-select:none;
    font-family: 'Neutrif Studio', sans-serif !important;
    scroll-behavior: smooth;
    overflow-x: hidden;
}
*{
  font-family: 'Neutrif Studio', sans-serif !important;
  
}

.otpinput{
  width:2rem;
  height: 2rem;
  text-align: center;
  border-radius: 5px;
}
.react-tel-input .form-control{
  width:100% !important;
}
::-webkit-scrollbar {
  width: 4px; 
}

::-webkit-scrollbar-thumb {
  background-color: #ececf7; 
  border-radius: 4px;
}

/* Mozilla Firefox */
::-moz-scrollbar {
  width: 4px; 
}

/* Mozilla Firefox */
::-moz-scrollbar-thumb {
  background-color: #ececf7; 
  border-radius: 4px; 
}

.underlined {
  position: relative;
  margin-right: 1rem;
  margin-bottom: 1rem;
}
.underlined:after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%; 
  transform: translateX(-50%);
  height: 9px;
  width: 50px;
  border: solid 3px #FEB853;
  border-color: #FEB853 transparent transparent transparent;
  border-radius: 35% 35% 0 0;
}

.pdfCard .react-pdf__Page__canvas{
  width: 50% !important;
  height: auto !important;
  margin: 10% !important;
  border-width: 1px;
  border-style: solid;
  }
  .pdfCard{
      cursor: pointer;
      background-color: transparent !important
      ;
  }
  .Example__container__document .react-pdf__Document{
      box-shadow: 10px 10px 20px #aaaaaa;
  }

  #loader {
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
  }

  #loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #f4f4f4;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
  }
  .loader-content {
    text-align: center;
  }
  .loader-logo {
    width: 150px;
    height: auto;
  }
  .progress-bar {
    width: 100%;
    height: 8px;
    background-color: #e0e0e0;
    border-radius: 4px;
    overflow: hidden;
    position: relative;
  }
  .progress-fill {
    height: 100%;
    background-color: #3498db;
    width: 00%;
    transition: width 0.3s ease;
  }
  .MuiFormHelperText-root{
    margin-left:0 !important;
  }
